.postBody {
  width: 1000px;
  margin: auto;
  margin-bottom: 50px;
  border: 1px solid #e5e5e5;
  box-shadow: -2px -2px 1px 1px #f5f5f5;
  border-radius: 15px;
}
.postPictureBody {
  margin-top: 40px;
  margin-bottom: 30px;
  margin-left: auto;
  margin-right: auto;
  height: 25em;
  width: 25em;
  border: 5px solid #e5e5e5;
  box-shadow: -2px -2px 1px 1px #f5f5f5;
  border-radius: 15px;
}
.classificationButton {
  margin: 10px;
}
.classificationButton div {
  margin: 10px;
  line-height: 20px;
  font-family: "Sunflower", sans-serif;
  font-size: 1.3em;
  font-weight: 600;
}
.missngPlace {
  margin: 20px;
  line-height: 20px;
  font-family: "Sunflower", sans-serif;
  font-size: 1.3em;
  font-weight: 600;
}
.iFound {
  margin: 20px;
  line-height: 20px;
  font-family: "Sunflower", sans-serif;
  font-size: 1.3em;
  font-weight: 600;
}
.location {
  margin: 10px;
}
.exactLocation {
  margin: 20px;
}
.dogInfo {
  margin: 5px 20px;
}
.sex {
  margin-left: 10px;
}
.sex span {
  line-height: 20px;
  font-family: "Sunflower", sans-serif;
  font-size: 1.3em;
  font-weight: 600;
}

.sexSelect {
  margin: 10px;
  width: auto;
  height: auto;
  font-size: 20px;
}
.explanation {
  margin-left: 20px;
  margin-bottom: 20px;
}
.explanation input {
  width: auto;
  height: auto;
  font-size: 20px;
}
.writer input {
  width: auto;
  font-size: 20px;
  text-align: center;
}
#citySelect {
  margin-left: 10px;
  width: auto;
  height: auto;
  font-size: 20px;
}
#districtSelect {
  margin-left: 10px;
  width: auto;
  height: auto;
  font-size: 20px;
}
#classificationSelect {
  margin-left: 10px;
  height: auto;
  width: auto;
  font-size: 20px;
}
.speciesSelect {
  margin: 10px;
  width: auto;
  height: auto;
  font-size: 20px;
}
.species span {
  line-height: 20px;
  font-family: "Sunflower", sans-serif;
  font-size: 1.3em;
  font-weight: 600;
}
.no {
  margin-left: 20px;
  margin-top: 10px;
  line-height: 20px;
  font-family: "Sunflower", sans-serif;
  font-size: 1.3em;
  font-weight: 600;
}
.writer {
  margin: 20px;
}
.writer div {
  margin-bottom: 10px;
  line-height: 20px;
  font-family: "Sunflower", sans-serif;
  font-size: 1.3em;
  font-weight: 600;
}
.title {
  margin: 20px;
}
.title div {
  margin-bottom: 10px;
  line-height: 20px;
  font-family: "Sunflower", sans-serif;
  font-size: 1.3em;
  font-weight: 600;
}
.title input {
  width: auto;
  text-align: left;
  font-size: 20px;
  height: auto;
}
.submitButton {
  text-align: center;
  margin-bottom: 20px;
}
.submitButton button {
  margin: 3px;
  padding: 6px 12px;
  font-size: 1.4em;
}
.exactLocation input {
  width: auto;
  height: auto;
  font-size: 20px;
}
.photoInput {
  width: auto;
  height: auto;
  font-size: 20px;
}
.photoInput input {
  font-size: 20px;
  margin-left: 30%;
  widows: auto;
  height: auto;
}
.photoInput button {
  font-size: 10px;
  margin-left: 30%;
  width: auto;
  height: auto;
}
/* input[type="file"] {
  color: transparent;
} */
.dogname span {
  line-height: 20px;
  font-family: "Sunflower", sans-serif;
  font-size: 1.3em;
  font-weight: 600;
}
.dogname input {
  margin: 10px;
  width: auto;
  height: auto;
  font-size: 20px;
}
.reward {
  line-height: 20px;
  font-family: "Sunflower", sans-serif;
  font-size: 1.3em;
  font-weight: 600;
}
.reward input {
  margin: 10px;
  width: auto;
  height: auto;
  font-size: 20px;
}
.photoUpload {
  text-align: center;
}
.photoUpload button {
  font-size: 13px;
  border: 3px solid #e5e5e5;
  box-shadow: -2px -2px 1px 1px #f5f5f5;
  border-radius: 15px;
}
.photoUpload button:hover {
  color: red;
}
.photoUpload input[type="file"] {
  width: 14em;
  font-size: 16px;
  border: 3px solid #e5e5e5;
  box-shadow: -2px -2px 1px 1px #f5f5f5;
  border-radius: 15px;
}
.photoUpload input[type="file"]:hover {
  color: red;
}
#img {
  height: 100%;
  width: 100%;
  object-fit: cover;
  border-radius: 10px;
}
